@import '../../../assets/sass/variables.scss';
.formOne_main {
	background-image: linear-gradient(rgba(0, 0, 0, 0.527), rgba(0, 0, 0, 0.5)),
		url('../../../assets/images/istockphoto-143918313-170667a.jpg');
	width: 100%;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	height: 100vh;
}
.formOne_heading {
	padding-left: 3%;
	padding-top: 2%;
}
.formOne_heading_text {
	font-size: 30px;
	font-family: $font-family;
	font-weight: 500;
	color: $primary-color;
}
.formOne_body_main {
	align-items: center;
	justify-content: center;
	width: 100%;
}
.formOne_body {
	padding-top: 2%;
	padding-bottom: 2%;
	text-align: center;
	align-items: center;
	justify-content: center;
	color: $primary-color;
	filter: brightness(100%);
	box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.5);
	border-radius: 15px;
}
.formOne_body_heading {
	font-family: $font-family;
	font-size: 30px;
	font-weight: bold;
}
.formOne_btn {
	background-color: $secondary-color;
	border: none;
	border-radius: 18px;
	width: 70%;
	margin-right: 30%;
	margin-top: 3%;
	height: 40px;
}
.formOne_btn:hover {
	background-color: $primary-color;
	color: $secondary-color;
}
.color {
	color: $secondary-color;
	font-weight: bold;
}
.color:hover {
	color: $secondary-color;
}
.formOne_inp {
	background: transparent !important;
	border-radius: 18px;

	color: $primary-color !important;
	text-align: center;
}
.formInput::-webkit-input-placeholder {
	color: $primary-color;
	position: relative;
}
.formInput {
	position: relative;
	background: transparent !important;
	border-radius: 100px;
	text-align: center;
	height: 40px;
	border: 1px solid $primary-color;
	color: $primary-color !important;
}
.formOne_main_inp1 {
	// width: 70%;
	background: transparent;
	position: relative;
}
.iconUser {
	position: absolute;
	top: 9px;
	left: 7px;
	background: transparent !important;
	color: $secondary-color;
}
.iconPass {
	position: absolute;
	top: 54%;
	left: 7px;
	background: transparent !important;
	color: $secondary-color;
}
.fuseri {
	position: absolute;
	top: 8.5%;
	left: 3px;
	color: $secondary-color;
	border: 1px solid $primary-color;
	background-color: $primary-color;
	border-radius: 50%;
	width: 30px;
	padding-bottom: 1%;
}
.formOne_main_inp2 {
	padding-top: 5%;
	position: relative;
	// width: 70%;
	color: $primary-color;
	filter: brightness(100%);
}
.fpassi {
	position: absolute;
	top: 57%;
	left: 4px;
	color: $secondary-color;
	border: 1px solid $primary-color;
	background-color: $primary-color;
	border-radius: 50%;
	width: 30px;
	padding-bottom: 1%;
}
.form {
	align-items: center;
}
.forgot {
	text-decoration: none;
	color: $primary-color;
	padding-left: 35%;
}
.forgot:hover {
	color: $primary-color;
}
.OR_Main {
	width: 25%;
	height: 13px;

	text-align: center;
	margin-left: 38%;
	margin-top: 3%;
}
.OR_inner {
	font-size: 1rem;
	color: $primary-color;
	padding: 0 10px;
	font-weight: bold;
}

.adduser {
	margin-top: 3%;
	color: $primary-color;
}
.icons {
	padding-right: 3%;
}
.icons_inner {
	border-radius: 50%;
	border: 1px solid grey;
	padding: 10px;
	margin-left: 2%;
}
.OR {
	overflow: hidden;
	text-align: center;
}
.OR:before,
.OR:after {
	background-color: $primary-color;
	content: '';
	display: inline-block;
	height: 1px;
	position: relative;
	vertical-align: middle;
	width: 60%;
}
.OR:before {
	right: 0.5em;
	margin-left: -50%;
}
.OR:after {
	left: 0.5em;
	margin-right: -50%;
}
.css-1uv8bk3 {
	padding: 0px !important;
}
